import React from "react";
import { useEffect } from "react";
import { useSearchParams } from "react-router-dom";
import UserApi from "../generator/src/api/UserApi";

const ActivateAccount = () => {
  const [searchParams] = useSearchParams();
  const email = searchParams.get("email");
  const userApi = new UserApi();

  useEffect(() => {
    if (!email) {
      console.error("Brak adresu email w parametrze URL. Przekierowanie na stronę logowania.");
      window.location.href = "/login";
    }
  }, [email]);

  const handleActivate = () => {

    userApi.activate(email, (error, data, response) => {
      if (error) {
        console.error("Błąd aktywacji:", error);
        return;
      }
      console.log("Response:", response);
      if (response.header.location) {
        window.location.href =  response.header.location;
      } else {
        console.error("Nie udało się aktywować konta lub brak URL do przekierowania.");
      }

    });
    
  };

  return (
    <div className="active-container">
      <form id="login-form" className="lr-form">
        <h3 style={{ color: "#776F4F", marginBottom: "15px", marginLeft: "200px" }}>
          Aktywuj konto:
        </h3>
        <h2 style={{ marginBottom: "30px" }}>{email}</h2>
        <button type="button" className="lr-button" onClick={handleActivate}>
          Aktywuj konto
        </button>
      </form>
    </div>
  );
};

export default ActivateAccount;
