import React, { useState, useEffect } from "react";
import "../styles/styles-add.css";
import EntryApi from "../generator/src/api/EntryApi";
import UserApi from "../generator/src/api/UserApi";

const Add = () => {
  const [date, setDate] = useState("");
  const [text, setText] = useState("");
  const [error, setError] = useState("");
  const api = new EntryApi();
  const userApi = new UserApi();

  useEffect(() => {
      userApi.getUser((error, data) => {
        if (error && error.status === 302) {
          console.log("Błąd 302. Przekierowanie.");
          window.location.href = "/login";
        } 
      });
    }, []);


  const handleSave = (e) => {
    e.preventDefault();
    if (!text.trim()) {
      setError("Treść wpisu nie może być pusta.");
      return;
    }

    const newEntry = {
      date: date || null,
      text,
    };

    console.log("Dane wysyłane do backendu:", newEntry);
    
    api.addEntry(newEntry, (error, data) => {
      if (error) {
        console.error("Error adding entry:", error);
        setError("Wystąpił problem podczas dodawania wpisu.");
      } else {
        window.location.href = "/main";
      }
    });
  };

  return (
    <div className="add-container">
      <h2>Nowy Wpis</h2>
      <div className="form-container">
        <form id="add-entry-form" onSubmit={handleSave}>
          <div className="form-group">
            <label htmlFor="entry-date">Data:</label>
            <input
              type="date"
              id="entry-date"
              name="entry-date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="entry-text">Treść:</label>
            <textarea
              id="entry-text"
              name="entry-text"
              rows="6"
              value={text}
              onChange={(e) => setText(e.target.value)}
            ></textarea>
          </div>
          {error && <p className="error-message">{error}</p>}
          <div className="form-group">
            <button type="submit" className="save-btn">
              Zapisz
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Add;
