import React, { useState, useEffect } from "react";
import AdminApi from "../generator/src/api/AdminApi";
import "../styles/styles-main.css";
import "../styles/styles.css";
import UserApi from "../generator/src/api/UserApi";

const Admin_Main = () => {

  const [users, setUsers] = useState([]);
  const [selectedUserLogs, setSelectedUserLogs] = useState([]);
  const api = new AdminApi();
  const userApi = new UserApi();

  useEffect(() => {
    api.getUsers((error, data) => {
      if (error && error.status === 302) {
        console.log("Błąd 302. Przekierowanie.");
        window.location.href = "/login";
      } 
      else if (error){
        console.error("Błąd podczas pobierania użytkowników:", error);
      }
      else {
        setUsers(data);
      }
    });
  }, []);

  const handleUserClick = (userId) => {
    api.getSecurityLogs({ userId }, (error, data) => {
      if (error) {
        console.error("Błąd podczas pobierania logów użytkownika:", error);
      } else {
        console.log("Lista logów:", data);
        setSelectedUserLogs(data); 
      }
    });
  };
  
  const handleMainPage = () =>{
    window.location.href = "/main";
  }

  const handleLogout = () => {
    userApi.logout((error) => {
      if (error) {
        console.error("Error during logout:", error);
        alert("Wylogowanie nie powiodło się. Spróbuj ponownie.");
      } else {
        window.location.href = "/login";
      }
    });
  };

  return (
    <div className="container">
      <button
        className="logout-btn"
        onClick={handleLogout}
      >
        Wyloguj się
      </button>
      <button
        className="logout-btn"
        style={{ width: "100px", marginLeft: "115px" }}
        onClick={handleMainPage}
      >
        Wpisy
      </button>
      <div className="main-container">
        <div className="left-container">
          <h2>Użytkownicy</h2>
          <ul className="entries-list" id="entries-list">
            {users.map((user) => (
              <li
                key={user.id}
                onClick={() => handleUserClick(user.id)}
              >
                {user.email || `Użytkownik ${user.id}`}
              </li>
            ))}
          </ul>
        </div>

        <div className="middle-container" style={{width:'1000px'}}>
          <h2 style={{margin:'10px'}}>Szczegóły konta</h2>
          <div style={{margin:'10px', width:'900px', maxHeight:'500px', overflowY: "auto"}}>
            {selectedUserLogs.length === 0 ? (
              <p>Wybierz użytkownika, aby zobaczyć szczegóły jego logów.</p>
            ) : (
              <ul>
                {selectedUserLogs.map((log, index) => (
                  <li key={index}>
                    {log.date
                      ? new Date(log.date).toLocaleString()
                      : "Brak daty"}{" "}
                    - {log.content || "Nieznana akcja"}
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Admin_Main;
