import React, { useState, useEffect } from "react";
import "../styles/styles-main.css";
import "../styles/styles.css";
import EntryApi from "../generator/src/api/EntryApi";
import UserApi from "../generator/src/api/UserApi"; 

const Main = () => {
  const [entries, setEntries] = useState([]);
  const [selectedEntry, setSelectedEntry] = useState(null);
  const [isEditing, setIsEditing] = useState(false);
  const [editContent, setEditContent] = useState("");
  const [editDate, setEditDate] = useState("");
  const [isAdmin, setIsAdmin] = useState(false);
  

  const api = new EntryApi();
  const userApi = new UserApi();

  useEffect(() => {
    api.getEntries({}, (error, data) => {
      if (error && error.status === 302) {
        console.log("Błąd 302. Przekierowanie.");
        window.location.href = "/login";
      }     
      else if (Array.isArray(data)) {
        setEntries(data);
      } else {
        setEntries([]);
      }
    });
    userApi.getUser((error, data) => {
      console.log(data)
      console.log(error)
      if (!error && data && data.userRole === "ADMIN") {
        console.log("użytkownik:" + data.userRole)
        setIsAdmin(true);
      }
    });
  }, []);
  

  const formatDate = (date) => {
    if (!date) return "";
    const parsedDate = new Date(date);
    return parsedDate.toISOString().slice(0, 10); 
  };

  const handleEntryClick = (entry) => {
    api.getEntryById(entry.id, (error, data) => {
      if (error) {
        console.error("Error fetching entry details:", error);
      } else {
        const entryData = {
          id: data.id,
          date: formatDate(data.date),
          text: data.text || ""
        };
      setSelectedEntry(entryData);
      setIsEditing(false);
      setEditContent(data.text || "");
      setEditDate(formatDate(data.date));
      }
    });
  };  

  const handleEditSave = () => {
    if (selectedEntry) {
      const updatedEntry = {
        ...selectedEntry,
        text: editContent,
        date: editDate,  
      };
  
      api.updateEntry(selectedEntry.id, updatedEntry, (error, data) => {
        if (error) {
          console.error("Error updating entry:", error);
        } else {
          setEntries(
            entries.map((entry) =>
              entry.id === selectedEntry.id ? data : entry
            )
          );
          setSelectedEntry(updatedEntry);

  
          setIsEditing(false);
        }
      });
    }
  };
  

  const handleEditCancel = () => {
    setIsEditing(false);
  };

  const handleDelete = () => {
    if (selectedEntry) {
      if (selectedEntry.id) {
        api.deleteEntry(selectedEntry.id, (error) => {
          console.log("Wpis który chcę usunąć: ", selectedEntry);
          if (error) {
            console.error("Error deleting entry:", error);
          } else {
            setEntries(entries.filter((entry) => entry.id !== selectedEntry.id));
            setSelectedEntry(null);
          }
        });
      }
    }
  };

  const handleLogout = () => {
    userApi.logout((error) => {
      if (error) {
        console.error("Error during logout:", error);
        alert("Wylogowanie nie powiodło się. Spróbuj ponownie.");
      } else {
        window.location.href = "/login";
      }
    });
  };

  const handleAdminPage = () =>{
    window.location.href = "/admin_main";
  }

  return (
    <div className="container">
      <button
        className="logout-btn"
        onClick={handleLogout}
      >
        Wyloguj się
      </button>
      {isAdmin && (
        <button
          className="logout-btn"
          style={{ width: "100px", marginLeft: "115px" }}
          onClick={handleAdminPage}
        >
          Admin
        </button>
      )}
      <div className="main-container">
        <div className="left-container">
          <h2>Wpisy</h2>
          <ul className="entries-list">
            {entries.map((entry) => (
              <li
                key={entry.id}
                onClick={() => handleEntryClick(entry)}
                style={{ cursor: "pointer" }}
              >
              {formatDate(entry.date)}
              </li>
              ))}
          </ul>
        </div>

        <div className="middle-container">
          {selectedEntry ? (
            <div id="entry-details">
              {isEditing ? (
                <div>
                  <h2>Edycja</h2>
                  <input
                    type="date"
                    id="entry-date"
                    name="entry-date"
                    style={{ width: "150px", marginLeft: "4px" }}
                    value={editDate}
                    onChange={(e) => setEditDate(e.target.value)}
                  />
                  <label>
                    <textarea
                      className="content"
                      value={editContent}
                      onChange={(e) => setEditContent(e.target.value)}
                      style={{ height: "350px" }}
                    />
                  </label>
                  <div>
                    <button className="edit-button" onClick={handleEditSave}>
                      Zatwierdź
                    </button>
                    <button className="edit-button" onClick={handleEditCancel}>
                      Anuluj
                    </button>
                  </div>
                </div>
              ) : (
                <>
                  <h2>Szczegóły wpisu</h2>
                  <p>{new Date(selectedEntry.date).toLocaleDateString("en-CA")}</p>
                  <div className="content">
                    <p style={{ margin: "5px" }}>{selectedEntry.text}</p>
                  </div>
                  <button className="edit-button" onClick={() => setIsEditing(true)}>
                    Edytuj
                  </button>
                  <button className="edit-button" onClick={handleDelete}>
                    Usuń
                  </button>
                </>
              )}
            </div>
          ) : (
            <p style={{ margin: "5px", fontSize: "24px", color: "#776F4F" }}>
              Wybierz wpis, aby zobaczyć jego szczegóły.
            </p>
          )}
        </div>

        <div className="right-container">
          <button
            className="add-entry-btn"
            onClick={() => (window.location.href = "/add")}
          >
            Dodaj nowy wpis
          </button>
        </div>
      </div>
    </div>
  );
};

export default Main;
